import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import RetweetOutlined from '@ant-design/icons/RetweetOutlined'
import ReadMore from '../../components/UI/ReadMore'
import { Divider, makeStyles, Paper, Typography } from '@material-ui/core'

import { useTranslation } from 'react-i18next'
import ResourceCore from '../../components/ResourceCore'
import ActivityTable from '../Goal/ActivityTable'
import MapPosition from '../../components/MapPosition/MapPosition'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const Activity = () => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [activity, setActivity] = useState(null)

    const getDay = day => {
        switch (day) {
            case '0':
                return t('g.sunday')
            case '1':
                return t('g.monday')
            case '2':
                return t('g.tuesday')
            case '3':
                return t('g.wednesday')
            case '4':
                return t('g.thursday')
            case '5':
                return t('g.friday')
            case '6':
                return t('g.saturday')

            default:
                return ''
        }
    }

    const getRepeatType = repeat => {
        switch (repeat) {
            case 'day':
                return t('g.everyDay')
            case 'weekday':
                return t('g.everyDayOfWeek')
            case 'week':
                return t('g.everyWeek')
            case 'month':
                return t('g.everyMonth')
            case 'year':
                return t('g.everyYear')
            default:
                return ''
        }
    }
    return (
        <ResourceCore type="activity" setResource={setActivity}>
            {activity && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.tracking')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ActivityTable activityId={activity._id} />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        {activity.repeat !== 'no' && (
                            <>
                                <p>
                                    <RetweetOutlined />
                                    {` ${t('g.repeats')} 
                            ${getRepeatType(activity.repeat) +
                                (activity.repeat === 'weekday'
                                    ? ' (' +
                                      activity.days.reduce(
                                          (acc, val, index) =>
                                              acc +
                                              (index > 0 ? ', ' : '') +
                                              getDay(val),
                                          ''
                                      ) +
                                      ').'
                                    : '.')}`}
                                </p>
                                <Divider
                                    light
                                    variant="middle"
                                    style={{ marginBottom: '15px' }}
                                />
                            </>
                        )}

                        <ReadMore
                            initialHeight={200}
                            contentEditor={activity.description}
                        />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={activity.position} />
                    </Paper>
                    {/* Used in goals
                    Associated rewards */}
                </>
            )}
        </ResourceCore>
    )
}

export default withRouter(Activity)
