import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../components/UI/ReadMore'
import { makeStyles, Paper, Typography } from '@material-ui/core'

import Rewards from '../../components/Rewards/Rewards'
import Activities from '../../components/Activities/Activities'
import { useTranslation } from 'react-i18next'
import ResourceCore from '../../components/ResourceCore'
import MapPosition from '../../components/MapPosition/MapPosition'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const Goal = () => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [progress, setProgress] = useState(null)
    return (
        <ResourceCore type="goal" setResource={setProgress}>
            {progress && progress._id && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ReadMore
                            initialHeight={200}
                            contentEditor={progress.description}
                        />
                    </Paper>

                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={progress.position} />
                    </Paper>
                    <Activities progress={progress} />
                    <Rewards progress={progress} />
                </>
            )}
        </ResourceCore>
    )
}

export default withRouter(Goal)
