import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import HorizontalScrollList from './UI/HorizontalScrollList'
import { useTranslation } from 'react-i18next'
import {
    CardActionArea,
    CardContent,
    CardMedia,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from './MasonryCard'

const SimilarResources = props => {
    const { t } = useTranslation()
    const { resource, type } = props
    const [similarResources, setSimilarResources] = useState([])
    const [similarIsRequested, setSimilarIsRequested] = useState(false)
    const title =
        type === 'goal'
            ? t('g.similarGoals')
            : type === 'reward'
            ? t('g.similarRewards')
            : t('g.similarActivities')
    const urlDir =
        type === 'goal' ? 'goals' : type === 'reward' ? 'rewards' : 'activitie'
    useEffect(() => {
        if (resource && resource._id && !similarIsRequested) {
            setSimilarIsRequested(true)
            axios
                .post('/api/similar', {
                    search: {
                        categories: resource.category || [],
                        position: resource.position,
                        currentId: resource._id,
                        type: type,
                    },
                })
                .then(res => {
                    if (res.data.success) {
                        setSimilarResources(res.data.resources)
                    }
                })
                .catch(err => {})
        }
    }, [resource && resource._id])
    return similarResources.length > 0 ? (
        <HorizontalScrollList
            title={title}
            items={similarResources}
            content={item => (
                <CardActionArea>
                    <Link to={'/' + urlDir + '/' + item._id}>
                        <CardMedia
                            style={{
                                height: '150px',
                                backgroundSize: 'contain',
                            }}
                            image={
                                item.images.length > 0
                                    ? item.images[0]
                                    : placeholderImages[getPH(item._id)]
                            }
                            title={item.name}
                        />
                        <CardContent style={{ padding: '5px' }}>
                            <Typography variant="body2" noWrap>
                                {item.name}
                            </Typography>
                        </CardContent>
                    </Link>
                </CardActionArea>
            )}
        />
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

export default withRouter(connect(mapStateToProps)(SimilarResources))
