import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../components/UI/ReadMore'
import { makeStyles, Paper, Typography } from '@material-ui/core'

import { useTranslation } from 'react-i18next'
import ResourceCore from '../../components/ResourceCore'
import MapPosition from '../../components/MapPosition/MapPosition'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const Reward = () => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [reward, setReward] = useState(null)

    return (
        <ResourceCore type="reward" setResource={setReward}>
            {reward && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ReadMore
                            initialHeight={200}
                            contentEditor={reward.description}
                        />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={reward.position} />
                    </Paper>
                    {/* Used in goals
                    Associated rewards */}
                </>
            )}
        </ResourceCore>
    )
}

export default withRouter(Reward)
